<template>
  <div>
    <div id="wrap" class="clearfix">
      <h1 class="userClass">预定须知</h1>
      <!--查询栏-->
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>产品筛选:</span>
          </el-form-item>
          <el-form-item>
            <el-input v-model="siteName"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search">查询</el-button>
            <el-button type="primary" @click="Add">新增</el-button>
          </el-form-item>
        </el-form>
      </el-col>

    </div>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    computed: mapGetters([
      'predeterminedInstructionsList'
    ]),
    data() {
      return {
        siteName: ''
      }
    },
    methods: {
      initData() {
        let getBookKnow = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "tm_bk_TourSiteID": "023",
        };
        this.$store.dispatch('initPredeterminedInstructions', getBookKnow)
          .then(()=>{

          },err=>{

          })
      },
      search() {
        this.initData()
      },
      Add() {

      },
    },
    created() {
      this.initData();
    }
  }
</script>
<style scoped>
</style>



